@charset "UTF-8";
/* ------------------------------------ *\
  QUOTES
\* ------------------------------------ */
.fr-quote {
  position: relative;
  margin: 0;
  padding: 0 0.5rem 2rem 0.5rem;
}

@media (min-width: 48em) {
  .fr-quote {
    padding: 0 0 0 2rem;
  }
}

.fr-quote::before {
  font: normal normal normal 2rem/1 dsfr-icons;
  content: "";
  display: block;
  padding-bottom: 0.5rem;
}

.fr-quote::after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0.5rem;
  width: calc(50% - 1rem);
  height: 100%;
  pointer-events: none;
}

@media (min-width: 48em) {
  .fr-quote::after {
    left: 0;
  }
}

.fr-quote--column {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}

@media (min-width: 48em) {
  .fr-quote--column {
    margin-left: 13rem;
  }
}

@media (min-width: 48em) {
  .fr-quote--column {
    min-height: 13rem;
  }
}

.fr-quote--column .fr-quote__author {
  margin-left: 5.25rem;
}

@media (min-width: 48em) {
  .fr-quote--column .fr-quote__author {
    margin-left: 0;
  }
}

.fr-quote--column .fr-quote__source {
  margin-left: 5.25rem;
}

@media (min-width: 48em) {
  .fr-quote--column .fr-quote__source {
    margin-left: 0;
  }
}

.fr-quote--column figcaption {
  min-height: 4.25rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}

@media (min-width: 48em) {
  .fr-quote--column figcaption {
    min-height: auto;
  }
}

.fr-quote blockquote {
  margin: 0;
}

.fr-quote blockquote p {
  margin-bottom: 1rem;
  font-size:  1.25rem ;
  line-height:  2rem ;
  font-weight: 700;
}

@media (min-width: 48em) {
  .fr-quote blockquote p {
    font-size:  1.375rem ;
    line-height:  2.25rem ;
  }
}

.fr-quote__author {
  font-weight: 700;
  font-size:  0.875rem ;
  line-height:  1.5rem ;
  margin-bottom: 0.25rem;
}

.fr-quote__image {
  width: 4.25rem;
  background-size: cover;
  border-radius: 50%;
  overflow: hidden;
  position: absolute;
  display: flex;
  flex-direction: row;
}

.fr-quote__image img {
  width: 100%;
  height: 100%;
}

@media (min-width: 48em) {
  .fr-quote__image {
    position: absolute;
    top: 50%;
    left: -13rem;
    width: 11.5rem;
    height: 11.5rem;
    margin-top: -5.75rem;
  }
}

.fr-quote cite {
  font-size:  0.75rem ;
  line-height:  1.25rem ;
  font-style: italic;
}

.fr-quote figcaption {
  --ul-type: none;
  --ol-type: none;
  --ul-start: 0;
  --ol-start: 0;
  --xl-block: 0;
  --li-bottom: 0;
  --ol-content: none;
}

.fr-quote figcaption li {
  font-size:  0.75rem ;
  line-height:  1.25rem ;
}

.fr-quote figcaption .fr-quote__source {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.fr-quote figcaption .fr-quote__source li {
  display: inline-block;
}

.fr-quote figcaption .fr-quote__source li:not(:last-child)::after {
  content: "·";
  display: inline-block;
  padding-left: 0.25rem;
  padding-right: 0.25rem;
}

.fr-quote::before {
  color: var(--bf500);
}

.fr-quote::after {
  box-shadow: inset 0 -1px 0 0 var(--g300);
}

@media (min-width: 48em) {
  .fr-quote::after {
    box-shadow: inset 1px 0 0 0 var(--g300);
  }
}
